<template>
  <div id="home" class="">
    <div class="header">
      <div class="header__hamburger">
        <a href="#">
          <img src="../assets/icon/main_menu_hamburger.png"
          alt="" class="folded"
          @click.prevent="open_category">
          <img src="../assets/icon/close_menu.png"
          alt="" class="cross"
          @click.prevent="close_category">
        </a>
      </div>
      <div class="header__nav">
        <div class="header__nav__leftArrow ">
          <a href="#"
          @click.prevent="scroll_list('header__nav', false)">
            <img src="../assets/icon/main_menu_arrow_left.png" alt="">
          </a>
        </div>
        <ul class="header__nav__list"
        @mouseenter="bind_list_scroll('header__nav')"
        @mouseleave="unbind_list_scroll">
          <li>
            <a href="#" data-path='online_store'
            @click="go_page('front/online_store/all')">
              <span class="chinese">線上購物</span>
              <span class="english">ONLINE STORE</span>
            </a>
          </li>
          <li>
            <a href="#" data-path='news'
            @click="go_page('front/news')">
              <span class="chinese">最新消息</span>
              <span class="english">NEWS</span>
            </a>
          </li>
          <li>
            <a href="#" data-path='about'
            @click="go_page('front/about')">
              <span class="chinese">關於我們</span>
              <span class="english">ABOUT</span>
            </a>
          </li>
          <li>
            <a href="#" data-path='order_info'
            @click="go_page('front/order_info')">
              <span class="chinese">購物指南</span>
              <span class="english">Order Info.</span>
            </a>
          </li>
          <li>
            <a href="#" data-path='contact'
            @click="go_page('front/contact')">
              <span class="chinese">聯絡我們</span>
              <span class="english">CONTACT</span>
            </a>
          </li>
        </ul>
        <div class="header__nav__rightArrow ">
          <a href="#"
          @click.prevent="scroll_list('header__nav', true)">
            <img src="../assets/icon/main_menu_arrow.png" alt="">
          </a>
        </div>
      </div>
      <ul class="header__sharelinks">
        <li>
          <a href="#">
            <img src="../assets/icon/main_menu_fb.png" alt="" class="m">
            <img src="../assets/icon/facebook_icon.png" alt="" class="l">
          </a>
        </li>
        <li>
          <a href="#">
            <img src="../assets/icon/main_menu_ig.png" alt="" class="m">
            <img src="../assets/icon/instagram_icon.png" alt="" class="l">
          </a>
        </li>
        <li>
          <a href="#">
            <img src="../assets/icon/main_menu_line.png" alt="" class="m">
            <img src="../assets/icon/line_icon.png" alt="" class="l">
          </a>
        </li>
      </ul>
    </div>
    <div class="banner">
      <ul class="banner__img">
        <transition name="fade"
        v-for="(item, index) in banner"
        :key="index">
          <li v-if="item.is_shown">
            <img :src="item.img" :alt="item.title">
          </li>
        </transition>
      </ul>
      <div class="banner__logo">
        <img src="../assets/banner_logo.png" alt="">
      </div>
      <div class="banner__store">
        <img src="../assets/icon/banner_store_link.png" alt="">
      </div>
      <div class="banner__link">
        <a href="#" :title="banner_link.title"
        @click="go_page(`front/online_store/all/${banner_link.path}`)"></a>
      </div>
      <ul class="banner__navList">
        <li
        v-for="(item, index) in banner"
        :key="index">
          <a href="#"
          @click.prevent="change_bgimg(index)">
            <div :class="{'active' : item.is_shown}"></div>
          </a>
        </li>
      </ul>
    </div>
    <div class="footer">
      <div class="footer__logo">
        <img src="../assets/footer_logo.png" alt="">
      </div>
      <div class="footer__copyright">
        COPYRIGHTCOPYRIGHT © ALL RIGHTS RESERVED.
      </div>
      <ul class="footer__contact">
        <li>
          <a href="#">Tel (02) 12345678</a>
        </li>
        <li>
          <a href="#">Line @lineme.tw</a>
        </li>
        <li>
          <a href="#">service@floraldesign.com</a>
        </li>
        <li>
          <span>100006 臺北市中正區重慶南路1段122號</span>
        </li>
      </ul>
    </div>
    <div class="category">
      <div class="category__leftArrow">
        <a href="#"
        @click.prevent="scroll_list('category', false)">
          <img src="../assets/icon/main_menu_arrow_left.png" alt="">
        </a>
      </div>
      <ul class="category__list"
      @mouseenter="bind_list_scroll('category')"
      @mouseleave="unbind_list_scroll">
        <li>
          <a href="#"
          @click="go_page('front/online_store/all')"
          >全部</a>
        </li>
        <li
        v-for="(item, index) in category"
        :key="index">
          <a href="#"
          @click="go_page(item.path)"
          >{{item.chinese}}</a>
        </li>
      </ul>
      <div class="category__rightArrow">
        <a href="#"
        @click.prevent="scroll_list('category', true)">
          <img src="../assets/icon/main_menu_arrow.png" alt="">
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
  props: ['products'],
  data() {
    return {
      banner: [],
      banner_link: {},
      category: [],
      bgimg_interval: null,
    };
  },
  methods: {
    go_page(pageString) {
      this.$router.push(`/${pageString}`);
    },
    set_interval() {
      this.bgimg_interval = setInterval(() => {
        let nextIndex = this.banner
          .findIndex((obj) => obj.is_shown === true);
        nextIndex = nextIndex === this.banner.length - 1 ? 0 : nextIndex + 1;
        this.banner
          .find((obj) => obj.is_shown === true).is_shown = false;
        this.banner[nextIndex].is_shown = true;
        this.banner_link = this.banner
          .find((obj) => obj.is_shown === true);
      }, 8000);
    },
    start_interval() {
      this.set_interval();
    },
    end_interval() {
      clearInterval(this.bgimg_interval);
    },
    open_category() {
      $('#home').addClass('category--open');
      $('.category__list').animate({
        left: 20,
        opacity: 1,
      });
      this.toggle_arrow('category');
    },
    close_category() {
      $('#home').removeClass('category--open');
      $('.category__list').css({
        left: 70,
        opacity: 0,
      });
    },
    toggle_arrow(elStr) {
      const parentWidth = $(`.${elStr}__list`).width();
      let childrenWidth = 0;
      document.querySelectorAll(`.${elStr}__list li`).forEach((li) => {
        childrenWidth += $(li).outerWidth(true);
      });
      childrenWidth = Math.floor(childrenWidth);
      const parentLeft = $(`.${elStr}__list`).scrollLeft();
      if (parentWidth < childrenWidth) {
        if (parentLeft === 0) {
          $(`.${elStr}__leftArrow a`).fadeOut();
        } else {
          $(`.${elStr}__leftArrow a`).fadeIn();
        }
        if (childrenWidth - parentLeft <= parentWidth) {
          $(`.${elStr}__rightArrow a`).fadeOut();
        } else {
          $(`.${elStr}__rightArrow a`).fadeIn();
        }
      } else {
        $(`.${elStr}__list`).scrollLeft(0);
        $(`.${elStr}__leftArrow a`).fadeOut();
        $(`.${elStr}__rightArrow a`).fadeOut();
      }
    },
    scroll_list(elStr, direction) {
      if (direction) {
        // true = scrollLeft++
        $(`.${elStr}__list`).animate({
          scrollLeft: '+=50',
        }, 'fast', () => {
          this.toggle_arrow(elStr);
        });
      } else {
        $(`.${elStr}__list`).animate({
          scrollLeft: '-=50',
        }, 'fast', () => {
          this.toggle_arrow(elStr);
        });
      }
    },
    bind_list_scroll(elStr) {
      $(window).bind('mousewheel', (e) => {
        if (e.originalEvent.deltaY > 0) {
          // 往下滑  scrollLeft++
          this.scroll_list(elStr, true);
        } else {
          this.scroll_list(elStr, false);
        }
      });
    },
    unbind_list_scroll() {
      $(window).unbind('mousewheel');
    },
    change_bgimg(index) {
      this.end_interval();
      this.banner
        .find((obj) => obj.is_shown === true).is_shown = false;
      this.banner[index].is_shown = true;
      this.banner_link = this.banner
        .find((obj) => obj.is_shown === true);
      this.start_interval();
    },
  },
  created() {
    this.banner = this.products
      .filter((obj) => obj.on_banner === true)
      .map((obj, index) => {
        const newObj = {};
        newObj.title = obj.title;
        newObj.img = obj.images.banner;
        newObj.path = obj.id;
        newObj.is_shown = index === 0;
        return newObj;
      });

    this.banner_link = this.banner
      .find((obj) => obj.is_shown === true);

    this.category = this.products
      .map((obj) => obj.category)
      .flat(1);
    this.category = [...new Set(this.category.map((item) => JSON.stringify(item)))]
      .sort()
      .map((item) => JSON.parse(item))
      .map((item) => {
        const newObj = {};
        newObj.path = `front/online_store/${item.english}`;
        newObj.chinese = item.chinese;
        newObj.english = item.english;
        return newObj;
      });
  },
  mounted() {
    document.title = 'FLORAL DESIGN 艸芊芳芫';
    this.toggle_arrow('header__nav');
    this.toggle_arrow('category');
    $(window).resize(() => {
      if ($(window).width() <= 1023) {
        this.toggle_arrow('header__nav');
        this.toggle_arrow('category');
      }
    });
    this.set_interval();
  },
};
</script>
